import React from 'react'
import styled from "styled-components";
import Scab from "../../assets/Scab";
import axios from "axios";
import MetamaskImg from "../../assets/img/metamask.png";
import { wallet_metamask, web3, wallet_walletConnect_disconnect} from "../../../config-web3";
import { useAlert } from 'react-alert';

function Step_Two(props) {
    const alert = useAlert();

    const createWallet = () => {
        axios.get("https://server.monstercoders.io/api/blockreview/user/eoa/create", {withCredentials : true})
            .then(res => {
                if(!res.data.success){
                    alert.error("  .   !");
                    return;
                }
                props.setUserPubKey(res.data.payload.address);
                props.setUserPrivateKey(res.data.payload.privateKey);
            });
    }


    const connectExternalWallet_Metamask = async() => {
        //  
        await wallet_metamask();
        // Get PubKey
        const getPubKey = await web3.eth.getAccounts();
        if(getPubKey[0]){
            props.setUserPubKey(getPubKey[0]);
        }
    }
    
    return (
        //    .
        //  =   API ;
        //  &  ;
        <Container>
            <TextContent>
            <div>  .</div>
                    <div>    
                        <LinkText onClick={()=> window.open("https://metamask.io/", "_blank")}>  </LinkText>
                     .
                    </div>
                    <div>       <LinkText onClick={()=> {
                        wallet_walletConnect_disconnect();
                        window.location.reload();
                        alert.info("   .")
                    }}></LinkText> !</div>
            </TextContent>
            
            <ScabContainer>
                <Scab name="" img="https://blockmonkey-assets.s3.ap-northeast-2.amazonaws.com/blockreview/walleticon.png" handleClick={createWallet}/>
                <Scab name="" img={MetamaskImg} handleClick={connectExternalWallet_Metamask}/>
            </ScabContainer>
        </Container>
    )
}

export default Step_Two


const Container = styled.div`
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 50px;
    border: 1px solid lightgray;
`;

const TextContent = styled.div`
    font-size: 18px;
    font-weight: 600;
    text-align: center;
    margin-bottom: 100px;
`;

const ScabContainer = styled.div`
    display:flex;
    justify-content: space-around;
`;
const LinkText = styled.span`
    color: blue;
    font-size: 18px;
    font-weight: 600;
    :hover{
        cursor: pointer;
    }
`;

