import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import Step_one from "./sections/Step_one";
import Step_two from "./sections/Step_two";
import Step_three from "./sections/Step_three";
import axios from "axios";
import { useAlert } from 'react-alert';
import {useNavigate} from "react-router-dom";

function Register(props) {
    const [RegisterStage, setRegisterStage] = useState(1); // 1 ~ 2 ~ 3;
    const [UserTypeCheck, setUserTypeCheck] = useState(null); // 0= ; 1=
    const [UserPubKey, setUserPubKey] = useState("");
    const [UserPrivateKey, setUserPrivateKey] = useState("");
    
    // State
    const [Email, setEmail] = useState("");
    const [Password, setPassword] = useState("");
    const [Nickname, setNickname] = useState("");
    const [Phone, setPhone] = useState("");
    const [StoreId, setStoreId] = useState("");

    const alert = useAlert();
    const navi = useNavigate();

    useEffect(() => {
        if(props.user.auth){
            alert.error("    ")
            navi('/')
        }
    }, [props.user])


    useEffect(() => {
        // UserType  2;
        if(UserTypeCheck !== null) {
            setRegisterStage(2);
        }
        if(UserPubKey){
            setRegisterStage(3);
        };
    }, [UserTypeCheck, UserPubKey]);

    //2 -> 3 ,  
    useEffect(async() => {
        if(UserPubKey !== ""){
            let payload = {
                element: "accountPubKey",
                content: UserPubKey
            }
            const result = await axios.post("https://server.monstercoders.io/api/blockreview/user/register/overlapcheck", payload, {withCredentials : true})
            if(!result.data.success){
                //?
                setUserPubKey("");
                setUserPrivateKey("");
                setRegisterStage(2);
                alert.error("  .");
            }
        }
    }, [UserPubKey])


    const renderingRegisterForm = () => {
        if(RegisterStage === 1){
            return (<Step_one setUserTypeCheck={setUserTypeCheck} />)
        }
        if(RegisterStage === 2){
            return (<Step_two setUserPubKey={setUserPubKey} setUserPrivateKey={setUserPrivateKey} />)
        }
        if(RegisterStage === 3){
            return (
            <Step_three 
                Email={Email}
                setEmail={setEmail}
                Password={Password}
                setPassword={setPassword}
                Nickname={Nickname}
                setNickname={setNickname}
                Phone={Phone}
                setPhone={setPhone}
                UserTypeCheck={UserTypeCheck}
                setUserTypeCheck={setUserTypeCheck}
                UserPubKey={UserPubKey}
                UserPrivateKey={UserPrivateKey}
                StoreId={StoreId}
                setStoreId={setStoreId}
            />)
        }
    }

    return (
        <RegisterContainer>
                <LogoBox>
                    <Img src="https://blockmonkey-assets.s3.ap-northeast-2.amazonaws.com/blockreview/logo2.png"/>
                </LogoBox>


                {/*   , 1 , 2 , 3   . */}
                <>
                    {renderingRegisterForm()}
                    
                </>
        </RegisterContainer>
    )
}

export default Register;

const RegisterContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;

const LogoBox = styled.div`
    width: 250px;
    height: 250px;
`;

const Img = styled.img`
    width: 100%;
`;

